import React, { Fragment, useEffect } from 'react'
import Spinner from '../layout/Spinner'
import Repos from '../repos/Repos.js'
import { Link } from 'react-router-dom'

const User = ({user, loading, repos, getUser, getUserRepos, match }) => {

  useEffect(()=> {
    getUser(match.params.login)
    getUserRepos(match.params.login)
    //eslint-disable-next-line
  }, []);
 
  const { name, company, avatar_url, location, bio, blog, login, html_url, followers, following, public_repos, public_gists, hireable } = user; 
      
  loading && <Spinner />

  return (
    <Fragment>
      <Link to='/' className='btn btn-light'>Back to search</Link>
      Hireable: {' '}
      {hireable ? <i className='fas fa-check text-success'/> : <i className='fas fa-times-circle text-danger' /> }
      <div className="card grid-2">
        <div className="all-center">
          <img src={avatar_url} className='round-img' style={{ width: '150px' }} />
          <h1>{name}</h1>
          <p>Location: {location}</p>
        </div>
        <div>
          {bio && (
            <Fragment>
              <h3>Bio</h3>
              <p>{bio}</p>
            </Fragment>
          )}
          <a href={html_url} className='btn btn-rk my-1'>Visit Github Profile</a>
          <ul>
            <li>
              {login && <Fragment>
                <p>Username: {login}</p>
              </Fragment>}
            </li>
            <li>
              {company && <Fragment>
                <p>Company: {company}</p>
              </Fragment>}
            </li>
            <li>
              {blog && <Fragment>
                <p>Website: {blog}</p>
              </Fragment>}
            </li>
          </ul>
        </div>
      </div>
      <div className="card text-center">
        <div className="badge badge-primary">Followers: {followers}</div>
        <div className="badge badge-success">Following: {following}</div>
        <div className="badge badge-light">Public Repos: {public_repos}</div>
        <div className="badge badge-dark">Public Gists: {public_gists}</div>
      </div>
      <Repos repos={repos} />
    </Fragment> 
  )  
}

export default User
