const appReducer = (state, action) => {
  switch (action.type) {
    case 'changeLoading':
      return {
        ...state,
        loading: !state.loading
      };
    case 'searchUsers':
      return {
        ...state,
        alert: null,
        loading: false,
        users: action.payLoad
      };
    case 'getUser':
      return {
        ...state,
        user: action.payLoad,
        loading: false,
        alert: null
      };
    case 'clearUsers':
      return {
        ...state,
        loading: false,
        users: [],
        alert: null
      };
    case 'getUserRepos':
      return {
        ...state,
        repos: action.payLoad,
        alert: null
      };
    case 'showAlert':
      return {
        ...state, 
        alert: action.payLoad,
      }
    default: return state;
  }
}

export default appReducer;