import React, { Fragment, useReducer } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import axios from 'axios';
import Navbar from './components/layout/Navbar.js'
import Alert from './components/layout/Alert.js'
import Users from './components/users/Users.js'
import User from './components/users/User.js'
import Search from './components/users/Search.js'
import About from './components/pages/About.js'
import appReducer from './appReducer.js'


const App = () => {
  
  // const [users, setUsers] = useState([]);
  // const [user, setUser] = useState({})
  // const [repos, setRepos] = useState([])
  // const [loading, setLoading] = useState(false)
  // const [alert, setAlert] = useState(null)

  const [state, dispatch] = useReducer(appReducer, {
    loading: false,
    users: [],
    user: {},
    repos: [],
    alert: null
  })

  const searchUsers = async (text) => {

    dispatch({type: 'changeLoading', payLoad: 'true'})
      
    const res = await axios.get(`https://api.github.com/search/users?q=${text}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`)
    
    dispatch({type: 'searchUsers', payLoad: res.data.items})
  }
   
  //Get a single github user
  const getUser = async (username) => {
    dispatch({type: 'changLoading', payLoad: 'true'})
      
    const res = await axios.get(`https://api.github.com/users/${username}?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`)
    
    dispatch({type: 'getUser', payLoad: res.data})
  }

  //Get Repositories from a single github user
  const getUserRepos = async (username) => {
    dispatch({type: 'changLoading', payLoad: 'true'})
      
    const res = await axios.get(`https://api.github.com/users/${username}/repos?per_page=5&sort=created:asc&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`)
    
    dispatch({type: 'getUserRepos', payLoad: res.data})
  }

  const clearUsers = () => {
    dispatch({type: 'clearUsers'})
  }

  const showAlert = (msg, type) => {
    dispatch({type: 'showAlert', payLoad: {msg, type}})
  }
  
  return (
    <Router>
      <div className='App' >
        <Navbar /> 
        <div className="container">
          <Switch>
            <Route exact path='/' render={props => {
              return (
                <Fragment>
                  <Alert alert={state.alert} />
                    <Search
                      searchUsers={searchUsers}
                      clearUsers={clearUsers}
                      showClear={state.users.length > 0 ? true : false}
                      setAlert = {showAlert}
                    />
                    <Users loading={state.loading}  users={state.users} />
                </Fragment>
              ) 
            }} />
            <Route exact path='/about' render={props => {
              return (
                <About />
              )
            }} />
            <Route path='/user/:login' render={props => {
              return (
                <User
                  {...props}
                  getUser={getUser}
                  getUserRepos={getUserRepos}
                  user={state.user}
                  repos={state.repos}
                  loading={state.loading} />
              )
            }}/>
          </Switch> 
        </div>
      </div>
    </Router>
  ) 
  
}

export default App;
